@import url("https://fonts.googleapis.com/css2?family=Tektur:wght@400;700&display=swap");

    html {
        background-image: linear-gradient(to right, #364a63 30%, #603c41 70%);
        color:rgb(255, 255, 255);
        font-family: "Tektur", cursive;
        position: relative;
    }
    
    #main {
        display: flex;
        flex-direction: row;
    }

    #photo{
        display: flex;
        margin: auto;
        border-radius: 50%;
        width: 80px;
        height: 80px;
        justify-self: center;
        padding: 0 20px;
        transition: 1s ease;
    }

    #photo:hover {
        display: flex;
        justify-self: center;
        width: 200px;
        height: 200px;
        margin:auto;
    }

    #space{
        flex-grow: 10;
    }

    #bio {
        padding-left: 10px;
        display: flex;
        flex-wrap: nowrap;
        flex-direction: column;
        justify-content: center;
        text-align: center;
    }

    #name {
        font-size: 40px;
    }

    #contact {
        flex-shrink: 1;
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-align: center;
        padding-right: 2%;
        text-align: center;
        font-size: large;
        
    }
    #icons{
        font-size: x-small;
    }
    #icons>img{
        width: 35px;
        height: 35px;
        margin:5px;
    }
    a>img{
        width: 35px;
        height: 35px;
        margin:5px;
    }
    #b{
        height: 5000px;
    }
    
    .my-link{
        font-size: larger;
        color: white;

    }

    .my-link:hover {
        color: rgb(0, 0, 0);
    }

      